import { mapState } from 'vuex';
export default {
	data() {
		return {
			sizeCalcState: false,
			tabScrollTop: 0,
			currentIndex: 0,
			list: [],
			flist: [],
			slist: [],
		}
	},
	onLoad(){
		this.loadData();
	},
	computed: {
		...mapState({
			app_info: state => state.app_info
		})
	},
	methods: {
		async loadData(cb){
			this.$http.get('category').then(res => {
				this.list = res.data
				if (this.list.length) {
					this.currentIndex = 0
					this.getSlist()
				}
				cb && cb()
			})
		},
		getSlist() {
			this.slist = this.list[this.currentIndex].childlist
		},
		//一级分类点击
		tabtap(item, index){
			this.currentIndex = index
			this.getSlist()
		},
		//右侧栏滚动
		asideScroll(e){
			if(!this.sizeCalcState){
				this.calcSize();
			}
			let scrollTop = e.detail.scrollTop;
			let tabs = this.slist.filter(item=>item.top <= scrollTop).reverse();
			if(tabs.length > 0){
				this.currentId = tabs[0].pid;
			}
		},
		//计算右侧栏每个tab的高度等信息
		calcSize(){
			let h = 0;
			this.slist.forEach(item=>{
				let view = uni.createSelectorQuery().select("#main-" + item.id);
				view.fields({
					size: true
				}, data => {
					item.top = h;
					h += data.height;
					item.bottom = h;
				}).exec();
			})
			this.sizeCalcState = true;
		},
		navToList(item){
			uni.navigateTo({
				url: `/pages/product/list?cat_id=` + item.id
			})
		}
	},
	onPullDownRefresh() {
		this.loadData(() => {
			uni.stopPullDownRefresh()
		})
	}
}
